/*
- Font sizes (px)
10 / 12 / 14 / 16 / 18 / 20 / 24 / 30 / 36 / 44 / 52 / 62 / 74 / 86 / 98

- Spacing system (px)
2 / 4 / 8 / 12 / 16 / 24 / 32 / 48 / 64 / 80 / 96 / 128
*/
body {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-weight: 400;
}

.container {
  margin: auto 0.25rem;
}
@media only screen and (min-width: 375px) {
  .container {
    margin: auto 1.5rem;
  }
}

/*
  1. Use a more-intuitive box-sizing model.
*/
*,
*::before,
*::after {
  box-sizing: border-box;
}

/*
  2. Remove default margin
*/
* {
  margin: 0;
  padding: 0;
}

/*
  3. Allow percentage-based heights in the application
*/
html,
body {
  height: 100%;
}

/*
  Typographic tweaks!
  4. Add accessible line-height
  5. Improve text rendering
*/
body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

/*
  6. Improve media defaults
*/
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

/*
  7. Remove built-in form typography styles
*/
input,
button,
textarea,
select {
  font: inherit;
}

/*
  8. Avoid text overflows
*/
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  color: hsl(192deg, 100%, 9%);
}

@media only screen and (min-width: 375px) {
  .footer__boxes {
    display: grid;
  }
  .footer__box {
    display: flex;
    flex-direction: column;
  }
  .footer__location {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: 0.5rem;
  }
  .footer__phone {
    display: flex;
    align-items: center;
    gap: 0.5rem;
  }
  .footer__email {
    display: flex;
    align-items: center;
    gap: 0.5rem;
  }
  .footer__icon {
    display: flex;
    font-size: 30px;
  }
}
@media only screen and (min-width: 375px) {
  .home {
    background-color: hsl(193deg, 100%, 96%);
    background-image: url(../../../images/bg-hero-mobile.svg);
  }
  .home__content .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 0;
    margin-bottom: 6rem;
  }
  .home__output {
    display: grid;
    justify-content: center;
    gap: 4rem;
  }
  .home__title {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 2rem;
  }
  .home__picture {
    margin-bottom: 3.5rem;
  }
  .home__heading {
    max-width: 360px;
    font-size: 29px;
  }
  .home__paragraph {
    font-size: 16px;
    max-width: 305px;
  }
}
@media only screen and (min-width: 375px) {
  .about__content {
    display: grid;
    gap: 2rem;
    padding: 4.5rem 0;
  }
  .about__card {
    display: grid;
    border: 1px solid black;
    border-radius: 16px;
  }
  .about__title {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 1rem;
    padding: 2rem 0;
  }
  .about__title-reverse {
    order: 2;
  }
  .about__heading {
    font-size: 25px;
  }
  .about__paragraph {
    font-size: 16px;
    min-width: 300px;
  }
  .about__img {
    padding: 2rem 2rem;
  }
}
.btn {
  border: none;
  border-radius: 50px;
  cursor: pointer;
  display: inline-block;
  font-weight: 700;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.header__button {
  font-size: 15px;
  padding: 0.5rem 2rem;
  background-color: hsl(0deg, 0%, 100%);
}
@media only screen and (min-width: 768px) {
  .header__button {
    padding: 0.5rem 2rem;
  }
}

.home__button {
  padding: 0.75rem 4rem;
  font-size: 15px;
  background-color: hsl(322deg, 100%, 66%);
  color: hsl(0deg, 0%, 100%);
}